/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

* {
  box-sizing: border-box; }

html {
  font-size: 100%; }

h1 {
  font-size: 4rem; }

.ct-container {
  display: flex;
  flex: 1 1 0%;
  height: 100vh; }

.ct-contacts {
  background-color: #511e7a;
  width: 30%;
  color: #fafafa; }

.ct-dialog {
  background-color: #fafafa;
  width: 70%; }

.ct-me {
  height: 150px;
  padding-top: 1.25rem; }
  .ct-me > * {
    margin: 0 auto;
    text-align: center; }
  .ct-me .ct-avatar {
    margin-bottom: 0.625rem; }

.ct-avatar {
  position: relative;
  width: 30px;
  height: 30px;
  background-color: orange;
  border-radius: 30px; }

.ct-avatar__image {
  width: 60px;
  height: auto; }

.ct-avatar--medium {
  width: 45px;
  height: 45px;
  border-right: 45px; }

.ct-avatar--large {
  width: 60px;
  height: 60px;
  border-radius: 60px; }

.ct-avatar__user-status {
  position: absolute;
  top: 3px;
  left: 3px;
  background-color: #405254;
  width: 10px;
  height: 10px;
  border-radius: 10px; }
  .ct-avatar__user-status:before {
    content: '';
    top: 1px;
    left: 1px;
    width: 8px;
    height: 8px;
    position: absolute;
    border-radius: 8px; }

.ct-avatar__user-status--online:before {
  background-color: #3ac141; }

.ct-contact {
  display: flex;
  flex: 1 1 0%;
  padding-top: 1.5rem;
  padding-bottom: 1.5rem; }
  .ct-contact:hover, .ct-contact:focus {
    background-color: #67269b; }

.ct-contact--active {
  background-color: #5f238e; }

.ct-contact-container {
  overflow-x: hidden;
  overflow-y: scroll;
  height: calc(100vh - 150px); }

.ct-contact__avatar-container {
  flex: 1;
  max-width: 90px; }
  .ct-contact__avatar-container > * {
    margin: 0 auto; }

.ct-contact__user-info-container {
  position: relative;
  flex: 1;
  margin-right: 0.9375rem; }

.ct-contact__user-last-message-time {
  position: absolute;
  top: 0;
  right: 0; }

.ct-contact__user-unread-message-number {
  position: absolute;
  right: 0;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  text-align: center;
  background-color: orange;
  width: 20px;
  height: 20px;
  border-radius: 20px; }
  .ct-contact__user-unread-message-number span {
    position: absolute;
    width: 100%;
    left: 0;
    right: 0;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%); }

.ct-dialog {
  display: flex;
  flex: 1 1 0%;
  flex-direction: column; }

.ct-dialog__top-bar {
  display: flex;
  flex: 1 1 0%;
  padding: 1.875rem;
  height: 75px;
  max-height: 75px;
  flex-direction: row;
  border-bottom: 2px solid #405254; }

.ct-dialog__messages-container {
  height: calc(100vh - 135px);
  flex: 0.8 1 0%;
  align-items: center;
  align-content: center;
  justify-content: flex-end;
  overflow-y: scroll;
  padding: 1.875rem;
  display: flex;
  flex-direction: column; }

.ct-dialog__send-bar {
  flex: 0.2 1 0%; }

#ct-message__send-div {
  display: flex;
  flex-direction: row;
  padding: 0.875rem; }

.ct-dialog__interlocutor {
  flex: 1 1 0%;
  display: flex;
  align-items: center;
  align-content: center;
  flex-direction: row;
  justify-content: flex-start; }

.ct-dialog__actions {
  display: flex;
  flex: 1 1 0%;
  flex-direction: row;
  max-width: 100%;
  justify-content: flex-end;
  align-content: center;
  align-items: center; }
  .ct-dialog__actions > * {
    display: inline-block;
    vertical-align: middle; }

.ct-message {
  display: flex;
  margin-top: 0.625rem;
  flex-direction: row;
  width: 100%;
  justify-content: flex-end; }

.ct-message--my {
  justify-content: flex-start; }
